@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#App{
  font-family: 'Poppins', sans-serif;
  background-color: gainsboro;
  min-height: 100vh;
}
ul{
    margin: 0 !important;
    padding: 0;
    list-style: none;
    display: flex;
    align-items: center;
    background: black;
}
.header__li{
    position: relative;
    text-decoration: none;
    color: white;
    font-size: 16px;
    font-weight: 600;
    cursor: pointer;
    padding: 16px 32px;
    text-transform: uppercase;
}
.header__li:not(:first-child)::after{
    content: "";
    position: absolute;
    left: 0;
    bottom: -16px;
    width: 100%;
    height: 6px;
    background-color: #2785F2;
    opacity: 0;
    transition: all .2s;
}
.header__li:hover{
    color: white;
}
.header__li.active::after{
    opacity: 1;
}
.header__li:hover::after{
    opacity: 1;
    transition: all .4s;
}
.header__li:first-child:hover::after{
    display: none;
}
.header__li:first-child img{
    width: 80px;
}
.footer{
    background-color: black;
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.footer__logo{
    max-width: 120px;
    margin: 12px 0;
}
.undeveloped{
    margin-top: 100px;
    text-align: center;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.undeveloped-img{
    max-width: 600px;
}

.undeveloped-notice{
    font-size: 24px;
}
/* Content */
.content {
  position: relative;
  padding: 20px 0;
}

/* Title */
.small-title,
.big-title,
.decs-title {
  display: block;
  text-align: center;
  font-style: italic;
}

.small-title,
.big-title {
  text-transform: uppercase;
  letter-spacing: 4px;
}

.big-title {
  font-size: 70px;
  font-weight: 800;
}

.decs-title {
  margin: 0 auto;
  max-width: 500px;
}

/* Item */
.item {
  position: relative;
  padding-top: 2%;
  overflow: hidden;
  cursor: pointer;
  max-height: 380px;
}
.item:hover .item__container img {
  transform: scale(1);
}
.item:hover .item__text {
  background-color: #036d88;
}
.item:hover .item__text span {
  transform: translate(12px, 0);
}
.item__container {
  position: relative;
  max-height: 100%;
  overflow: hidden;
  box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.4);
}
