/* Content */
.content {
  position: relative;
  padding: 20px 0;
}

/* Title */
.small-title,
.big-title,
.decs-title {
  display: block;
  text-align: center;
  font-style: italic;
}

.small-title,
.big-title {
  text-transform: uppercase;
  letter-spacing: 4px;
}

.big-title {
  font-size: 70px;
  font-weight: 800;
}

.decs-title {
  margin: 0 auto;
  max-width: 500px;
}

/* Item */
.item {
  position: relative;
  padding-top: 2%;
  overflow: hidden;
  cursor: pointer;
  max-height: 380px;
}
.item:hover .item__container img {
  transform: scale(1);
}
.item:hover .item__text {
  background-color: #036d88;
}
.item:hover .item__text span {
  transform: translate(12px, 0);
}
.item__container {
  position: relative;
  max-height: 100%;
  overflow: hidden;
  box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.4);
}
