.item__container img {
  width: 100%;
  transform: scale(1.05);
  transition: all 0.5s ease;
}
.item__text {
  position: absolute;
  bottom: 0;
  font-weight: bold;
  padding: 8px;
  background-color: #061c25;
  width: 100%;
  transition: all 0.5s ease;
}

.item__text span {
  display: block;
  color: white;
  margin-left: 12px;
  text-transform: uppercase;
  font-style: italic;
  transition: all 0.5s ease;
}

/* Character info */
.character-info {
  width: 100%;
  height: 1000px;
  background: linear-gradient(black 1%, transparent 10%),
    linear-gradient(to top, black 2%, transparent 100%),
    url("https://api.genshin.dev/characters/albedo/constellation");
  background-position: center;
  background-size: cover;
  padding: 20px 0;
}

.character-info__dir {
  background-color: transparent;
  color: white;
  list-style: none;
}
.character-info__dir-item {
  margin: 0 8px;
}
.character-info__dir-item-link {
  text-decoration: none;
  color: white;
  text-transform: capitalize;
}

.character-info__dir-item-link:hover {
  color: rgb(206, 193, 19);
}

.character-info__dir-icon {
  margin: 0 8px;
}
